.navigation {
  color: black;
}

.navigation-choosen {
  cursor: pointer;
  background-color: #e9c8c1;
  color: white;
}

.navigation:hover {
  cursor: pointer;
  border-bottom: 2px solid #e9c8c1;
  color: #e9c8c1;
}

.navigation-active {
  cursor: pointer;
  color: #e9c8c1;
}

.navigation-line {
  border: 1px solid #e9c8c1;
  border-radius: 5px;
}
